@import "styles/colors";

.modal-container {
  position: relative;
}

.modal-container .modal,
.modal-container .modal-backdrop {
  position: absolute;
}

.modal-header {
  background: $cost-blue;
  color: white;
  padding: 20px;
  position: relative;
  border-radius: 0;
  h4 {
    font-size: 20px;
  }
  .delete-icon {
    position: absolute;
    right: 0;
    width: 28px;
    height: 28px;
    background: url("../../assets/images/ic_delete_white_48px.svg") no-repeat center
      center;
    margin: 20px;
    top: 0;
    background-size: cover;
    cursor: pointer;
  }
}

.modal-content {
  border-radius: 0;
  border: 0;
}

.modal-body {
  padding: 20px;
  .label {
    color: $cost-blue;
    display: block;
    text-align: left;
    border-radius: 0;
    padding: 15px 0 5px;
    font-size: 14px;
    margin-bottom: 0;
    &:first-child {
      padding-top: 0;
    }
  }
  input {
    border: 2px solid $cost-blue;
    width: 100%;
    height: 50px;
    color: $cost-blue;
    padding-left: 10px;
    font-size: 18px;
  }
  input[type="number"] {
    -moz-appearance: textfield;
  }
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }
  .dropdown {
    margin-bottom: 0;
  }
}

.modal-footer {
  padding: 20px;
  text-align: center;
  border-top: 1px solid $cost-blue;
  border-radius: 0;
  flex-wrap: nowrap !important;
  .btn {
    border-radius: 0;
    width: calc(50% - 5px);
    border: 2px solid $cost-blue;
    height: 50px;
    font-size: 16px;
  }
  .btn-default {
    color: $cost-blue;
    background: $light-gray;
  }
  .btn-primary {
    color: white;
    background-color: $cost-blue !important;
    margin-left: 10px !important;
    border: 0 !important;
  }
}

.confirm-modal {
  .modal-body {
    font-size: 18px;
    color: $cost-blue;
  }
  .delete-icon {
    cursor: default;
  }
}

.ok-modal {
  .modal-body {
    font-size: 20px;
    color: $cost-blue;
  }
  .modal-footer {
    .btn-primary {
      margin-left: 0 !important;
      width: 100%;
    }
  }
}

.line-chart-modal:not(.small),
.bar-chart-modal:not(.small) {
  width: 100vw !important;
  max-width: 100vw;
  .modal-dialog {
    width: 80vw !important;
    max-width: 80vw !important;
    margin: 0.5rem auto !important;
  }
  @media screen and (max-width: 575.98px) {
    .modal-dialog {
      width: 100vw !important;
      max-width: 100vw !important;
      min-height: 100%;
      margin: 0 !important;
    }
  }
}

.line-chart-modal.small,
.bar-chart-modal.small {
  @media screen and (max-width: 575.98px) {
    .modal-dialog {
      width: 100vw !important;
      max-width: 100vw !important;
      min-height: 100%;
      margin: 0 !important;
    }
  }
}
